

.komanda__wrapper{
    position: relative;
    z-index: 20;
    // backdrop-filter: blur(10px);
    background-color: #f5f0ec;
    padding-top: 100px;
}
.komanda__container{
    position: relative;
    z-index: 20;
    width: 100%;
    height: 100%;
    margin: 0 0 0 auto;
    overflow: hidden;
    .komanda__text{
        font-family: "BeatriceBold";
        text-transform: uppercase;
        font-weight: 700;
        
        .komanda__text__container{
            display: flex;
            align-items: center;
            height: 170px;
            overflow: hidden;
            font-size: 200px;
            @media (max-width:978px){
                font-size: 150px;
                height: 130px;
            }
            @media (max-width:600px){
                font-size: 100px;
                height: 85px;
            }
            h4{
                color: #222;
            }
            span{
                color: transparent;
                -webkit-text-stroke: 2px #222;
            }
            img{
                width: 100%;
                height: 100%;
            }
        }
        
    }
}
